import Vue from 'vue'
import Router from 'vue-router'
import store from './store'

const Feed = () => import('./components/views/Feed.vue')
const Home = () => import('./components/views/Home.vue')
const BeerDetails = () => import('./components/views/BeerDetails.vue')
const Login = () => import('./components/views/Login.vue')
const UserDetails = () => import('./components/views/UserDetails.vue')

Vue.use(Router)

const redirectIfLoggedIn = async (to, from, next) => {
  await new Promise(resolve =>
    setTimeout(() => {
      resolve()
    }, 100)
  )
  if (store.state.userInfo.id) {
    next('/feed')
  } else {
    next()
  }
}

const checkAuth = async (to, from, next) => {
  await new Promise(resolve =>
    setTimeout(() => {
      resolve()
    }, 100)
  )
  if (!store.state.userInfo.id) {
    next('/')
  } else {
    next()
  }
}

export const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      beforeEnter: redirectIfLoggedIn,
    },
    {
      path: '/feed',
      name: 'feed',
      component: Feed,
      beforeEnter: checkAuth,
    },
    {
      path: '/beer/:id',
      name: 'beerDetails',
      component: BeerDetails,
      props: true,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      props: true,
    },
    {
      path: '/user/:id',
      name: 'userDetails',
      component: UserDetails,
      props: true,
    },
  ],
})

