<template>
  <div id="app">
    <navbar @logout="logout()" />
    <router-view/>
  </div>
</template>

<script>
import Navbar from './components/dumb/Navbar'
import { getUser } from './database/queries'

export default {
  components: {
    Navbar,
  },
  // auto log in from token
  apollo: {
    user: {
      query: getUser,
      variables: {
        detailed: false,
        history: false,
      },
      result({ data: { user: { id, username, friends } } }) {
        this.$store.commit('setUserInfo', { id, username, friends })
      },
      skip() {
        return !localStorage.getItem('token')
      },
      error(err) {
        console.warn(err)

        //localStorage.removeItem('token')
      },
    },
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo
    },
  },
  methods: {
    logout() {
      this.$store.commit('logOut')
      localStorage.removeItem('token')
      this.$router.push('/')
    },
  },
}
</script>


